      let cityArr = ["ÏÑúÏö∏", "Î∂ÄÏÇ∞", "Ïù∏Ï≤ú", "ÎåÄÍµ¨", "Í¥ëÏ£º", "ÎåÄÏ†Ñ", "Ïö∏ÏÇ∞", "Í≤ΩÍ∏∞", "Í∞ïÏõê", "Ï∂©Î∂Å", "Ï∂©ÎÇ®", "Í≤ΩÎ∂Å", "Í≤ΩÎÇ®", "Ï†ÑÎ∂Å", "Ï†ÑÎÇ®", "Ï†úÏ£ºÌäπÎ≥ÑÏûêÏπòÎèÑ"];

